#loginselector .buttons .button.twitch:hover {
  background-color: #9c83e0;
}

#loginselector .buttons .button:hover {
  box-shadow: 0 0 10px #0000004d;
}

#loginselector .buttons .button:active {
  border-top: 2px solid #0000004d;
  border-bottom: none;
}

#loginselector .disclaimer {
  padding: 12px 0 16px;
}

.block-right {
  position: absolute;
  right: 10px;
}

.block-left {
  position: absolute;
  left: 10px;
}

.block-center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  left: 0;
  right: 0;
}

.player-name {
  cursor: pointer;
}

#player-context-menu {
  color: #fff;
  z-index: 100;
  background-color: #000;
  position: absolute;
}

#player-context-menu #menu-title, #player-context-menu .menu-item {
  border-bottom: 1px solid #222;
  height: 24px;
  padding-top: 5px;
  padding-left: 20px;
  padding-right: 20px;
}

#player-context-menu .menu-item {
  cursor: pointer;
}

#player-context-menu .menu-item:last-child {
  border-bottom: none;
}

#player-context-menu .menu-item:hover {
  background-color: #222;
}

#player-context-menu #menu-title, #player-context-menu #menu {
  cursor: default;
  background-color: #222;
  font-weight: bold;
}

#player-context-menu #menu-stats {
  color: silver;
  cursor: default;
  height: auto;
  padding-left: 40px;
}

#player-context-menu #menu-stats:hover {
  background-color: #000;
}

#upgradeSelection {
  width: 50px;
  padding: 0;
  font-size: 14pt;
  position: absolute;
  top: 320px;
  left: 10px;
}

#upgradeSelection div {
  cursor: pointer;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  background-color: #6464964d;
  border: 1px solid #2f4f4f;
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  line-height: 50px;
}

#upgradeSelection div:hover {
  background-color: #64649699;
}

#upgradeSelection .stats-val {
  color: #fff;
  margin-top: -15px;
  margin-left: 15px;
  font-family: Courier New, Courier, monospace;
  font-size: 24pt;
  display: block;
}

#upgradeSelection i {
  color: #888;
  margin-top: 5px;
  margin-left: -23px;
  display: block;
}

#upgradeSelection #drop-upgrade {
  margin-top: 20px;
}

#upgradeSelection #drop-upgrade img {
  width: 30px;
  height: auto;
  margin-top: 7px;
}

#actionButtons {
  width: 50px;
  padding: 0;
  font-size: 10pt;
  position: absolute;
  top: 570px;
  left: 10px;
}

#actionButtons .button {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  color: silver;
  background-color: #6464964d;
  border: 1px solid #2f4f4f;
  width: 150px;
  height: 25px;
  margin-bottom: 5px;
  padding-top: 5px;
  padding-left: 5px;
}

#actionButtons .button:hover {
  background-color: #64649699;
}

#ctf-info {
  text-align: center;
  color: #fff;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  font-size: 14pt;
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
}

#ctf-info .team-blue {
  color: #80bfff;
}

#ctf-info .team-red {
  color: #f66;
}

#stats {
  text-align: center;
  color: silver;
  width: 350px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12pt;
  position: absolute;
  top: 90px;
  left: 0;
  right: 0;
}

#stats #stats-row {
  display: flex;
}

#stats #stats-row #stats-left {
  text-align: right;
  border-right: 1px solid silver;
  flex: 50%;
  padding-right: 10px;
}

#stats #stats-row #stats-right {
  text-align: left;
  flex: 50%;
  padding-left: 10px;
}

#stats #stats-row .stat-val {
  color: #fff;
  font-family: Courier New, Courier, monospace;
  font-size: 14pt;
}

#server-message {
  text-align: center;
  color: #fff;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  font-size: 20pt;
  font-weight: bold;
  position: absolute;
  top: 230px;
  left: 0;
  right: 0;
}

#server-message .flag {
  width: 40px;
  height: 45px;
  margin-top: 20px;
  margin-right: 5px;
  display: inline-block;
}

#server-message .redflag {
  background-image: url("red-flag.38d5df77.png");
  width: 40px;
  height: 45px;
  margin-top: 20px;
  margin-right: 5px;
  display: inline-block;
}

#server-message .blueflag {
  background-image: url("blue-flag.8059e137.png");
  width: 40px;
  height: 45px;
  margin-top: 20px;
  margin-right: 5px;
  display: inline-block;
}

#aircraftSelection {
  width: 50px;
  padding: 5px;
  position: absolute;
  top: 10px;
  left: 10px;
}

#aircraftSelection img {
  cursor: pointer;
  width: 40px;
  height: auto;
  transform: rotate(30deg);
}

#aircraftSelection img#select-spirit {
  width: 110px;
  margin-left: -35px;
}

#aircraftSelection img#select-tornado {
  width: 42px;
}

#aircraftSelection img#select-mohawk, #aircraftSelection img#select-prowler {
  width: 46px;
}

#chat-input {
  width: 360px;
  height: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
}

#chat-input span {
  color: #fff;
}

#chat {
  color: #fff;
  background-color: #6464964d;
  border: 1px solid #2f4f4f;
  width: 350px;
  height: 200px;
  padding: 5px;
  position: absolute;
  top: 30px;
  right: 10px;
  overflow: auto;
}

#chat .chat-team {
  background-color: green;
}

#chat .chat-whisper {
  background-color: purple;
}

#chat .chat-warning {
  background-color: #8b0000;
}

#minimap {
  width: 400px;
  height: 200px;
  position: absolute;
  bottom: 0;
  right: 10px;
}

#minimap img {
  opacity: .5;
}

#player-list {
  color: #fff;
  width: 370px;
  height: calc(100% - 500px);
  position: absolute;
  top: 280px;
  right: -20px;
  overflow: hidden auto;
}

#player-list tr.playerlist-player-inactive td {
  text-decoration: line-through;
}

#player-list tr.playerlist-team-blue td {
  color: #80bfff;
}

#player-list tr.playerlist-team-red td {
  color: #f66;
}

#player-list td.playerlist-pos {
  width: 40px;
}

#player-list td.playerlist-flag {
  width: 30px;
}

#player-list td.playerlist-name {
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
  max-width: 200px;
  overflow: hidden;
}

#player-list td.playerlist-score {
  width: 120px;
}

#debugPanel {
  color: #fff;
  white-space: pre;
  background-color: #6464964d;
  border: 1px solid #2f4f4f;
  padding: 20px;
  position: absolute;
  bottom: 20px;
  left: 10px;
}

canvas.shake-hit {
  animation: .3s shake-hit;
}

canvas.shake-kill {
  animation: .5s shake-kill;
}

@keyframes shake-hit {
  0% {
    transform: translate(1px, 1px)rotate(0);
